import React from 'react';

import './BoxSample.css';

class BoxSample extends React.Component{


    render(){
        return (
        <div className="contactBox" id={this.props.id}>
          <div className="contactHeader">{this.props.mainTitle}</div>
          <div className="child-items">
            { this.props.children }
          </div>
          <p />
        </div>
    )
  }
}

export default BoxSample;